body {
  font-family: Arial, sans-serif;
  background-color: #e9f0f7;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.app-container {
  background-color: #ffffff;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  width: 400px;
}

.title {
  font-size: 20px;
  font-weight: bold;
  color: #b83939;
  margin-bottom: 20px;
}

.form-container {
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 10px;
}

label {
  display: block;
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 5px;
  color: #333333;
}

input {
  width: 100%;
  padding: 8px;
  border: 1px solid #cccccc;
  border-radius: 5px;
  font-size: 14px;
  outline: none;
  box-sizing: border-box;
}

input:focus {
  border-color: #007bff;
}

.button-group {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

button {
  padding: 10px 20px;
  font-size: 14px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  color: white;
  transition: background-color 0.2s ease-in-out;
}

.start-button {
  background-color: #28a745;
}

.start-button:hover {
  background-color: #218838;
}

.stop-button {
  background-color: #dc3545;
}

.stop-button:hover {
  background-color: #c82333;
}

.reset-button {
  background-color: #007bff;
}

.reset-button:hover {
  background-color: #0056b3;
}

.status-button {
  background-color: #17a2b8;
}

.status-button:hover {
  background-color: #138496;
}

.status-display {
  margin-top: 20px;
  text-align: center;
}

.status-display h2 {
  font-size: 18px;
  color: #333333;
}

.status-display p {
  font-size: 20px;
  font-weight: bold;
  color: #007bff;
}
